import { notFound } from "next/navigation";
import Link from "next/link";
import { Projekte } from "@/data/Projekte";

interface ProjectDetailPageProps {
  params: Promise<{ slug: string }>;
}

export async function generateStaticParams() {
  return Projekte.map((project) => ({
    slug: project.slug,
  }));
}

export default async function ProjectDetailPage({ params }: ProjectDetailPageProps) {
  const { slug } = await params;
  const project = Projekte.find((p) => p.slug === slug);

  if (!project) {
    notFound();
  }

  const getStatusStyles = () => {
    switch (project.status) {
      case "completed":
        return "bg-green-500/20 text-green-400 border-green-500/30";
      case "in-progress":
        return "bg-yellow-500/20 text-yellow-400 border-yellow-500/30";
      case "planned":
        return "bg-blue-500/20 text-blue-400 border-blue-500/30";
      case "beta-test":
        return "bg-purple-500/20 text-purple-400 border-purple-500/30";
      default:
        return "bg-gray-500/20 text-gray-400 border-gray-500/30";
    }
  };

  const getStatusLabel = () => {
    switch (project.status) {
      case "beta-test":
        return "Beta Test";
      case "in-progress":
        return "In Progress";
      default:
        return project.status.charAt(0).toUpperCase() + project.status.slice(1);
    }
  };

  return (
    <main className="min-h-screen bg-black text-white">
      {/* Back Button */}
      <div className="fixed top-6 left-6 z-50">
        <Link
          href="/"
          className="flex items-center gap-2 px-4 py-2 bg-[#1B244A] hover:bg-[#252f5a] rounded-lg border border-gray-700 hover:border-gray-500 transition-all text-gray-300 hover:text-white"
        >
          <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 19l-7-7 7-7" />
          </svg>
          Back
        </Link>
      </div>

      {/* Hero Section */}
      <section className="pt-24 pb-12 px-4">
        <div className="max-w-4xl mx-auto">
          {/* Status Badge */}
          <span
            className={`inline-block px-4 py-2 rounded-full text-sm font-medium border mb-6 ${getStatusStyles()}`}
          >
            {getStatusLabel()}
          </span>

          {/* Title */}
          <h1 className="text-4xl md:text-5xl font-bold mb-6">{project.title}</h1>

          {/* Short Description */}
          <p className="text-xl text-gray-400 leading-relaxed">{project.description}</p>
        </div>
      </section>

      {/* Main Content */}
      <section className="py-12 px-4">
        <div className="max-w-4xl mx-auto">
          {/* Long Description */}
          {project.longDescription && (
            <div className="mb-12">
              <h2 className="text-2xl font-bold mb-4 text-white">About this Project</h2>
              <p className="text-gray-300 leading-relaxed text-lg">{project.longDescription}</p>
            </div>
          )}

          {/* Tech Stack */}
          {project.techStack && project.techStack.length > 0 && (
            <div className="mb-12">
              <h2 className="text-2xl font-bold mb-4 text-white">Tech Stack</h2>
              <div className="flex flex-wrap gap-3">
                {project.techStack.map((tech, index) => (
                  <span
                    key={index}
                    className="px-4 py-2 bg-[#1B244A] text-gray-300 rounded-lg border border-gray-700 text-sm font-medium"
                  >
                    {tech}
                  </span>
                ))}
              </div>
            </div>
          )}

          {/* Features */}
          {project.features && project.features.length > 0 && (
            <div className="mb-12">
              <h2 className="text-2xl font-bold mb-4 text-white">Key Features</h2>
              <ul className="space-y-3">
                {project.features.map((feature, index) => (
                  <li key={index} className="flex items-start gap-3">
                    <svg
                      className="w-5 h-5 text-green-400 mt-0.5 flex-shrink-0"
                      fill="none"
                      stroke="currentColor"
                      viewBox="0 0 24 24"
                    >
                      <path
                        strokeLinecap="round"
                        strokeLinejoin="round"
                        strokeWidth={2}
                        d="M5 13l4 4L19 7"
                      />
                    </svg>
                    <span className="text-gray-300">{feature}</span>
                  </li>
                ))}
              </ul>
            </div>
          )}

          {/* Project Link */}
          {project.projectUrl && project.projectUrl !== "/" && (
            <div className="pt-8 border-t border-gray-800">
              <a
                href={project.projectUrl}
                target="_blank"
                rel="noopener noreferrer"
                className="inline-flex items-center gap-2 px-6 py-3 bg-white text-black font-bold rounded-lg hover:bg-gray-200 transition-all hover:scale-105"
              >
                Visit Project
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path
                    strokeLinecap="round"
                    strokeLinejoin="round"
                    strokeWidth={2}
                    d="M10 6H6a2 2 0 00-2 2v10a2 2 0 002 2h10a2 2 0 002-2v-4M14 4h6m0 0v6m0-6L10 14"
                  />
                </svg>
              </a>
            </div>
          )}
        </div>
      </section>
    </main>
  );
}
